<template>
    <div class="swiper">
        <swiper :options="swiperOption">
        <swiper-slide v-for="(item, index) in carouselList" :key='index'>
            <img :src="item.src">
        </swiper-slide>
        </swiper>
    </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    props:['carouselList'],
      components: {
      swiper,
      swiperSlide  
    },
    data () {
         return{
          swiperOption: {
          // debugger
          debugger: true,
          initialSlide :1,
          autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },
          autoplayDisableOnInteraction : false,
          slidesPerView: "auto",//sliderslides(carousel)loop 'auto'slidesloop'auto'loopedSlides
          centeredSlides: true//<span style="color:rgb(68,68,68);font-family:'microsoft yahei';font-size:13px;">true</span>
    },
      }
    },
}
</script>
<style scoped>
.swiper{
    width:100%;
    height: 4.5rem;
    overflow: hidden;
    position: relative;
 }
 .swiper-slide{
     width: 100%;
     height:4.5rem;
     text-align: center;
 }
 
 img{
     margin-top: 0;
     width:100%;
     height:100%;
 }
 img{
display: block;

margin: 0 auto;

margin-top: 3.5%;

width: 92.625%;

height: 92.625%;  

vertical-align: middle;
 
-webkit-transition: all 1s ease 0s;

-moz-transition: all 1s ease 0s;

-ms-transition: all 1s ease 0s;

-o-transition: all 1s ease 0s;

transition: all 1s ease 0s;
 
 }
</style>


