<template>
    <div>
            <span @click="rouer"> </span>
    </div>
</template>
<script>
export default {
    methods:{
        rouer(){
            this.$router.push({
                path:'/Home'
            })
        }
    }
}
</script>
<style scoped>
    div span{
    text-align: center;
    display: block;
    margin-top:3rem;
    }
</style>


