import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/index'
import User from '@/components/user'
import Follow from '@/components/follow'
import myCircle from '@/components/Circle'
import Login from '@/components/login'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/dist',
      name: 'dist',
      component:Login,
      meta:{
        title:''
      }
    },
    {
      path: '/Home',
      name: 'Home',
      component:Home,
      meta:{
        title:''
      }
    },
    {
      path: '/User',
      name: 'User',
      component: User,
      meta:{
        title:''
      }
    },
    {
      path: '/Follow',
      name: 'Follow',
      component: Follow,
      meta:{
        title:''
      }
    },
    {
      path: '/myCircle',
      name: 'myCircle',
      component: myCircle,
      meta:{
        title:''
      }
    },
  ]
})



